import React from "react";
import Nav from "./nav";

const Saved = props => (
  <div className="container">
    <Nav dest="/classinfo" edit=""></Nav>
    <div className="attendance-grid">
      <div className="attendance-head expand">
        <p className="title">Baptisms</p>
        <p className="item-name">Enter the number of baptisms this week.</p>
      </div>
      <div className="expand">
        <input name="saved" type="text" onChange={props.handleChange}></input>
      </div>
      <button className="prev button" onClick={props.prevStep}>
        PREV
      </button>
      <button className="next button" onClick={props.done}>
        DONE
      </button>
    </div>
  </div>
);

export default Saved;
